.page-wrapper {
  position: relative;
  z-index: 0;
}
.header-overlay {
  position: absolute;
  z-index: 980;
  left: 0;
  right: 0;
}
.page-wrapper :not(.uk-sticky-below) > .uk-navbar-container {
  padding-top: 10px;
  padding-bottom: 10px;
}

.uk-navbar-container {
  border-bottom: 1px solid #eaeaea;
}
.uk-button-primary {
  text-transform: capitalize;
  border-radius: 500px;
  line-height: 42px;
  background: linear-gradient(320deg, $global-primary-background, #ec2145);
  &:hover {
    background: linear-gradient(320deg, #ec215d, #ec2123);
  }
}
.uk-button-secondary {
  text-transform: capitalize;
  line-height: 42px;
  border-radius: 500px;
  transition: all 0.3s;
  &:hover {
    background-color: #ec2178;
    color: #fff;
    background-image: linear-gradient(320deg, #ec2178, #ec2145);
  }
}
.uk-text-background {
  font-size: 64px;
  line-height: 76.8px;
  font-weight: 600;
  background-color: #ec2178;
  color: transparent !important;
  background-image: linear-gradient(
    150deg,
    #2f3b8a 0,
    #60165e 20%,
    #de2867 70%,
    #f4d000 100%
  );
  @media screen and (max-width: $breakpoint-medium) {
    font-size: 46.2px;
    line-height: 55.44px;
  }
}
.uk-subnav > * > :first-child {
  color: $base-selection-color;
  text-transform: capitalize;
  font-size: 16px;
  line-height: 24px;
  &:hover {
    color: $global-primary-background;
  }
}
.uk-button-text::before {
  content: none;
}
a.uk-button.uk-button-text {
  text-transform: capitalize;
  color: $base-selection-color;
  &:hover {
    color: $global-primary-background;
    &:after {
      border-color: $global-primary-background;
    }
  }
  &:after {
    content: "";
    position: absolute;
    width: 40px;
    border-top: 2px solid $base-selection-color;
    left: -48px;
    top: calc(50% - 1px);
  }
}
.uk-divider-small::after {
  border-top: 2px solid $global-primary-background;
}
.uk-icon-link {
  color: $base-selection-color;
  &:hover {
    color: $global-primary-background;
  }
}

.uk-navbar-nav {
  & > li {
    position: relative;
    & > a {
      position: relative;
      padding: 0 24px;
      transition: all 0.1 ease-in-out;
      &::before {
        content: "";
        position: absolute;
        display: block;
        background: $global-primary-background;
        right: 24px;
        left: 24px;
        height: 1.3px;
        bottom: 0;
        visibility: hidden;
        transition: all 0.1 ease-in-out;
      }
    }
    &:hover {
      & a::before {
        visibility: visible;
      }
    }
  }
  &.uk-active {
    height: 2px;
  }
}

.uk-navbar-dropdown-nav {
  & > li {
    & > a {
      color: $global-color;
      font-size: 16px;
      letter-spacing: -0.64px;
      font-weight: 400;
      font-family: $global-font-family;
      &:hover {
        color: $global-primary-background;
      }
    }
  }
}
.uk-navbar-toggle {
  color: $global-color;
  &:hover {
    color: $global-primary-background;
  }
}
.uk-offcanvas-bar {
  a,
  .uk-close {
    color: $global-color;
    &:hover {
      color: $global-primary-background;
    }
  }
  .uk-nav-sub a {
    color: #919291;
    &:hover {
      color: $global-primary-background;
    }
  }
}

.uk-input {
  border: none;
  background-color: #fafafa;
  &:focus {
    background: #f5f5f5;
    outline: none;
  }
}
